@font-face {
  font-family: 'Pokemon';
  src: url('../fonts/Pokemon.ttf'); }
@font-face {
  font-family: 'Roboto';
  src: url('../fonts/Roboto.ttf'); }

* {
  box-sizing: border-box; }

body {
  margin: 0;
  padding: 0; }

h1, h2, h3, h4, p {
  margin: 0; }

.container {
  display: flex;
  flex-flow: column wrap;
  align-items: center;
  margin-top: 4rem; }

.header {
  background-color: #de3136;
  width: 100vw;
  position: fixed;
  top: 0;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
  z-index: 1000;
  padding: 0 1.5rem;
  & a:first-of-type {
    color: #f3e038;
    text-decoration: none;
    font-size: 1.5rem;
    padding-left: .5rem;
    margin-top: -1rem;
    font-family: 'Pokemon'; }
  & input {
    margin: .75rem;
    padding: .25rem; } }

.main {
  display: flex;
  flex-flow: row wrap;
  width: 100%;
  justify-content: space-evenly;
  font-family: Roboto; }

.pokemonCard {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  border-radius: .3rem;
  width: 30vw;
  max-width: 100%;
  margin-top: 1rem;
  box-shadow: 0px 2px 5px 1px #0000002e;
  .pokemonCardTop {
    padding: .25rem .5em;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    width: 100%;
    border-radius: .3rem;
    box-shadow: 0px 2px 5px 1px #eff4f5;
    background-color: #ddd;
    & h3::first-letter {
      display: inline-block;
      text-transform: capitalize; } }
  .pokemonCardBot {
    display: flex;
    flex-flow: row wrap;
    width: 100%;
    justify-content: space-between;
    padding: .3rem; } }

.highlighter {
  position: sticky;
  top: 30vh;
  display: flex;
  flex-flow: column wrap;
  border-radius: .3rem;
  width: 50vw;
  height: 50vh;
  max-width: 100%;
  margin-top: 1rem;
  box-shadow: 0px 2px 5px 1px #0000002e;
  & .pokemonCard {
    margin-top: 0;
    width: 100%;
    box-shadow: none;
    & .highlighted {
      width: 50%;
      & .pokemonSprite img {
        width: 100%;
        height: 100%; } } } }

.typing {
  display: flex;
  flex-flow: row-reverse;
  height: 1.5rem;
  & span {
    width: 3.4rem;
    text-align: center;
    background-size: 300%; } }
